import React, { Component, Fragment } from "react"
import Item from "./Item"
import "./styles/ficha.css"
class Ficha extends Component {
    render() {
        let info = Object.keys(this.props.data)
        return (
            <div className="container">
                {console.log(this.props.data)}
                <div className="row">
                    <div className="col-12 form-group name">
                        <h1>{this.props.data.nombre}</h1>
                    </div>
                    {this.props.edit ?
                        <div className="col-12 form-group name">
                            <button type="button" className="btn btn-outline-success newInfo" onClick={this.props.agregarNuevaInfo}>Agregar Nueva Informacion</button>
                        </div>
                        :
                        null
                    }
                    {info.map(item => (
                        (item === "nombre" ?
                            null
                            :
                            (item === "infoExtra" ?
                                <div className="col-12 form-group text-center d-flex align-items-center">
                                    <Item editarCliente={this.props.editarCliente} edit={this.props.edit} seccion={this.props.seccion} cliente={this.props.data.nombre} name={item} data={this.props.data[item]} />
                                </div>
                                :
                                <div className="col-12 col-md-6 form-group text-center d-flex align-items-center">
                                    <Item editarCliente={this.props.editarCliente} edit={this.props.edit} seccion={this.props.seccion} cliente={this.props.data.nombre} name={item} data={this.props.data[item]} />
                                </div>
                            )
                        )))}
                </div>
            </div>
        )
    }
}
export default Ficha

