import React, { Component } from "react"
import { Link, Redirect } from "react-router-dom"
import Ficha from "./Ficha"
import FichaPedidos from "./FichaPedidos"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowLeft, faTrash, faPlus, faEdit } from '@fortawesome/free-solid-svg-icons'
import * as firebase from "firebase"
import "./styles/ficha.css"
import { confirmAlert } from 'react-confirm-alert'; // Import
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import css
import {connect} from 'react-redux'
class FichaCliente extends Component {
    state = {
        edit: false,
        redirect: false
    }
    constructor(props) {
        const firebaseConfig = {
            apiKey: "AIzaSyDPmTjxjMxN2abofP8ZczgFGeYQYSvbXHE",
            authDomain: "stock-119e8.firebaseapp.com",
            databaseURL: "https://stock-119e8.firebaseio.com",
            projectId: "stock-119e8",
            storageBucket: "stock-119e8.appspot.com",
            messagingSenderId: "1007277754269",
            appId: "1:1007277754269:web:d1db0def0da907b2"
        }
        if (!firebase.apps.length) {
            firebase.initializeApp(firebaseConfig);
        }
        super(props)
    }
    agregarArticulo(articulo, accion) {
        const arr = []
        arr.push({
            nombre: articulo,
            cantidad: accion
        })
        this.setState({
            pedido: arr
        })
    }

    eliminarCliente = () => {
        confirmAlert({
            title: 'Eliminar Cliente?',
            buttons: [
                {
                    label: 'Eliminar',
                    onClick: async () => {
                        console.log(this.props.seccion)
                        await firebase.database().ref().child(this.props.user.uid).child(this.props.seccion).child(this.props.proovedor.datos.nombre).remove().then(
                            this.props.handleClick()
                        )
                    }
                },
                {
                    label: 'Cancelar'
                }
            ]
        });
    };
    async agregarInfoExtra() {
        let arrayInfo
        if (this.props.proovedor.datos.infoExtra) {
            arrayInfo = this.props.proovedor.datos.infoExtra
        }
        else {
            arrayInfo = []
        }
        arrayInfo.push(prompt("Ingresar Informacion Extra"))
        await firebase.database().ref().child(this.props.user.uid).child("clientes").child(this.props.proovedor.datos.nombre).child("datos").update(
            {
                infoExtra: arrayInfo
            }
        )


    }
    async agregarNuevaInfo() {
        const propiedad = prompt("Ingresar el Titulo de la Informacion ej:DNI")
        const info = prompt("Ingresar La informacion")
        await firebase.database().ref().child(this.props.user.uid).child("clientes").child(this.props.proovedor.datos.nombre).child("datos").update(
            {
                [propiedad]: info
            }
        )
    }
    logOut() {
        firebase.auth().signOut()
    }
    editarCliente() {
        this.setState({
            edit: !this.state.edit
        })

    }
    render() {
        return (
            <div>
                <div className="productList">
                    <div className="container-fluid">
                        <div className="row nav">
                            <div className="col-5 offset-1 text-left">
                                <Link to={{
                                    pathname: "/clientes"
                                }}>
                                    <h1> <FontAwesomeIcon className="backIcon" icon={faArrowLeft} onClick={e => {
                                        this.props.handleClick()
                                        this.editarCliente()
                                    }} />
                                    </h1>
                                </Link>
                            </div>
                            <div className="col-3 offset-3 danger-zone">
                                <h1> <FontAwesomeIcon className="backIcon" icon={faPlus} onClick={e => {
                                    this.agregarInfoExtra()
                                }} /></h1>
                                <h1> <FontAwesomeIcon className="backIcon" icon={faEdit} onClick={e => {
                                    this.editarCliente()
                                }} /></h1>
                                <h1> <FontAwesomeIcon className="backIcon" icon={faTrash} onClick={e => {
                                    this.eliminarCliente()
                                }} /></h1>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center form-group">
                                <Ficha
                                    agregarNuevaInfo={e => {
                                        this.agregarNuevaInfo().then(
                                            this.editarCliente()
                                        )
                                    }}
                                    editarCliente={e => {
                                        this.editarCliente()
                                    }}
                                    edit={this.state.edit}
                                    seccion={this.props.seccion}
                                    data={this.props.proovedor.datos}
                                />
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-12 text-center">
                                <h2 className="name">Lista de Pedidos</h2>
                            </div>
                            <div className="col-12 text-center form-group pt-2">
                                <FichaPedidos data={this.props.proovedor.pedidos} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = state =>({
    user:state.user,
})
export default connect(mapStateToProps,null)(FichaCliente)